@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
:root {
  --primary-color: #5dde7f;
  --secondary-color: #444466;
}

html,
body {
  font-family: 'Raleway', sans-serif;
  padding: 0;
  margin: 0;
}

#root {
  height: 100vh;
}

* {
  box-sizing: border-box;
}

.joinOuterContainer {
  display: flex;
  justify-content: center;
  text-align: center;
  height: 100vh;
  align-items: center;
  background: #333;
  /* background: rgb(68, 68, 102);
  background: linear-gradient(
    128deg,
    rgba(68, 68, 102, 1) 0%,
    rgba(93, 222, 127, 1) 53%
  ); */
}

.joinInnerContainer {
  width: 20%;
}

.joinInput {
  border: none;
  border-radius: 4px;
  padding: 8px 20px;
  width: 100%;
  font-size: 12px;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  font-family: inherit;
}

.joinInput:focus {
  box-shadow: inset -1px -1px 4px 2px rgba(0, 0, 0, 0.5);
  -webkit-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
}

.joinInput::-webkit-input-placeholder {
  color: #ddd;
  text-transform: uppercase;
}

.joinInput:-ms-input-placeholder {
  color: #ddd;
  text-transform: uppercase;
}

.joinInput::-ms-input-placeholder {
  color: #ddd;
  text-transform: uppercase;
}

.joinInput::placeholder {
  color: #ddd;
  text-transform: uppercase;
}

.heading {
  color: #5dde7f;
  color: var(--primary-color);
  font-size: 2.5em;
  padding-bottom: 10px;
  border-bottom: 2px solid;
}

.button {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  font-weight: 18px;
  letter-spacing: 0.6px;
  cursor: pointer;
  background: #5dde7f;
  background: var(--primary-color);
  padding: 20px;
  border-radius: 4px;
  display: inline-block;
  border: none;
  width: 100%;
  font-family: inherit;
}

.ripple {
  background-position: center;
  -webkit-transition: background 0.8s;
  transition: background 0.8s;
}
.ripple:hover {
  background: #444466 radial-gradient(circle, transparent 1%, #444466 1%) center/15000%;
  background: var(--secondary-color) radial-gradient(circle, transparent 1%, var(--secondary-color) 1%) center/15000%;
}
.ripple:active {
  background-color: #6eb9f7;
  background-size: 100%;
  -webkit-transition: background 0s;
  transition: background 0s;
}

.mt-20 {
  margin-top: 20px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .joinOuterContainer {
    height: 100%;
  }

  .joinInnerContainer {
    width: 90%;
  }
}

button:focus {
  outline: 0;
}

.howToContainer {
  color: white;
}

.howToContainer h3{
  margin-top: 40px;
}

:root {
  --primary-color: #5dde7f;
  --secondary-color: #444466;
}

.textContainer {
  display: flex;
  flex-direction: column;
  margin-left: 100px;
  color: white;
  height: 60%;
}

.activeContainer {
  display: flex;
  align-items: center;
  margin-bottom: 50%;
}

.activeItem {
  display: flex;
  align-items: center;
}

.activeContainer img {
  padding-left: 10px;
}

.textContainer h1 {
  margin-bottom: 0px;
}

.textContainer h1 > span {
  color: #5dde7f;
  color: var(--primary-color);
  font-weight: 900;
}

@media (min-width: 320px) and (max-width: 1200px) {
  .textContainer {
    display: none;
  }
}


.messages {
  padding: 5% 0;
  overflow: auto;
  flex: auto;
}
.messageBox {
  background: #f3f3f3;
  border-radius: 20px;
  padding: 5px 20px;
  color: white;
  display: inline-block;
  max-width: 80%;
}

.messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 1.1em;
  word-wrap: break-word;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 3px;
}

.sentText {
  display: flex;
  align-items: center;
  font-family: inherit;
  color: #828282;
  letter-spacing: 0.3px;
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}

.colorWhite {
  color: white;
}

.colorDark {
  color: #353535;
}

.backgroundBlue {
  background: var(--primary-color);
}

.backgroundLight {
  background: #f3f3f3;
}
.infoBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--primary-color);
  border-radius: 4px 4px 0 0;
  height: 60px;
  width: 100%;
}

.leftInnerContainer {
  flex: 0.5 1;
  display: flex;
  align-items: center;
  margin-left: 5%;
  color: white;
}

.rightInnerContainer {
  display: flex;
  flex: 0.5 1;
  justify-content: flex-end;
  margin-right: 5%;
}

.onlineIcon {
  margin-right: 5%;
}
.form {
  display: flex;
  border-top: 2px solid #D3D3D3;
}

.input {
  border: none;
  border-radius: 0;
  padding: 5%;
  width: 80%;
  font-size: 1.2em;
}

input:focus, textarea:focus, select:focus{
  outline: none;
}

.sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: var(--primary-color);
  padding: 20px;
  display: inline-block;
  border: none;
  width: 20%;
  cursor: pointer;
  border-radius: 4px;
}

.ripple {
  background-position: center;
  -webkit-transition: background 0.8s;
  transition: background 0.8s;
}
.ripple:hover {
  background: var(--secondary-color) radial-gradient(circle, transparent 1%, var(--secondary-color) 1%) center/15000%;
}
.ripple:active {
  background-color: #6eb9f7;
  background-size: 100%;
  -webkit-transition: background 0s;
  transition: background 0s;
}
.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #1A1A1D;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #FFFFFF;
  border-radius: 8px;
  height: 60%;
  width: 35%;
}

@media (min-width: 320px) and (max-width: 480px) {
  .outerContainer {
    height: 100%;
  }

  .container {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .container {
    width: 60%;
  }
}
